/* global jQuery MktoForms2*/
/*
 * Group last two words in radio button label to improve line wrapping
 */
(function ($) {

	"use strict";

	MktoForms2.whenReady(function () {
		$(document).on("cleanMarketoHTMLAndCSSComplete", function () {
			var labelText = $(".qmktoRadioListLabel").text();
			var labelTextArray = labelText.split(" ");
			var lastWord = labelTextArray.pop();
			var secondFromLastWord = labelTextArray.pop();
			var firstWords = labelTextArray.join(" ");
			var newLabel = firstWords + ' <span class="nowrap">' + secondFromLastWord + ' ' + lastWord + '</span> ';
			$(".qmktoRadioListLabel").html(newLabel);
		});
	});

}(jQuery));